import React, { useEffect, useState } from "react";
import libraryData from "../../../utils/librarySeoul/seoulCurrentLibrary.json";
import axios from "axios";
import styled from "styled-components";
import { Typography } from "@mui/material";
import "../../templates/map/map.css";

export function catchPosition(item) {
  let position = {
    x: item.xcnts,
    y: item.ydnts,
  };
  return position;
}

export default function KakaoMap({ selectedPosition, setSelectedPosition }) {
  let MAPKEY = process.env.REACT_APP_KAKAO_JS_API_KEY;

  useEffect(() => {
    const kakaoMapScript = document.createElement("script");
    kakaoMapScript.async = false;
    kakaoMapScript.src = `//dapi.kakao.com/v2/maps/sdk.js?appkey=${MAPKEY}&autoload=false`;
    document.head.appendChild(kakaoMapScript);

    const onLoadKakaoAPI = () => {
      // load    undefined
      window.kakao.maps.load(() => {
        var container = document.getElementById("map");
        var options = {
          center: new window.kakao.maps.LatLng(
            37.55292879706055,
            126.96941027090685
          ),
          level: 3,
        };

        // map 
        var map = new window.kakao.maps.Map(container, options);

        //   
        if (navigator.geolocation) {
          // GeoLocation    
          navigator.geolocation.getCurrentPosition(function (position) {
            var lat = position.coords.latitude, // 
              lon = position.coords.longitude; // 

            var locPosition = new window.kakao.maps.LatLng(lat, lon); //    geolocation   

            //   

            selectedPosition?.x > 0 ? panTo() : displayMarker(locPosition);
          });
        } else {
          // HTML5 GeoLocation         
          var locPosition = new window.kakao.maps.LatLng(33.450701, 126.570667),
            message = "geolocation  ..";

          displayMarker(locPosition);
        }

        var positions = libraryData.DATA.map((item) => ({
          title: item.lbrry_name,
          webAddress: item.hmpg_url,
          address: item.adres,
          tel: item.tel_no,
          restDate: item.fdrm_close_date,
          time: item.op_time,
          latlng: new window.kakao.maps.LatLng(item.xcnts, item.ydnts),
        }));

        //    
        var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

        for (var i = 0; i < positions.length; i++) {
          //     
          var imageSize = new window.kakao.maps.Size(24, 35);

          //   
          var markerImage = new window.kakao.maps.MarkerImage(
            imageSrc,
            imageSize
          );

          //  
          var marker = new window.kakao.maps.Marker({
            map: map, //   
            position: positions[i].latlng, //   
            title: positions[i].title, //  ,     
            image: markerImage, //  
            clickable: true,
          });

          //    
          var infowindow = new window.kakao.maps.InfoWindow({
            content: `<div class="mapInfo">
            <p class="title">${positions[i].title}</p>
            <p class="time">${positions[i].address}</p>
            <p class="time"> : ${positions[i].time}</p>
            <p class="time"> : ${positions[i].restDate}</p>
            <p class="time"> : ${positions[i].tel}</p>
            </div>`,
          });

          window.kakao.maps.event.addListener(
            marker,
            "mouseover",
            makeOverListener(map, marker, infowindow)
          );
          window.kakao.maps.event.addListener(
            marker,
            "mouseout",
            makeOutListener(infowindow)
          );
        }

        //     
        function makeOverListener(map, marker, infowindow) {
          return function () {
            infowindow.open(map, marker);
          };
        }

        //     
        function makeOutListener(infowindow) {
          return function () {
            infowindow.close();
          };
        }
        function panTo() {
          const moveLatLon = new window.kakao.maps.LatLng(
            selectedPosition.x,
            selectedPosition.y
          );
          map.panTo(moveLatLon);
        }

        //     
        function displayMarker(locPosition) {
          //  
          var marker = new window.kakao.maps.Marker({
            map: map,
            position: locPosition,
          });

          //    
          map.setCenter(locPosition);
        }

        //          
        var zoomControl = new window.kakao.maps.ZoomControl();
        map.addControl(zoomControl, window.kakao.maps.ControlPosition.RIGHT);

        //     
        marker.setMap(map);
      });
    };

    //    
    kakaoMapScript.addEventListener("load", onLoadKakaoAPI);
  }, [selectedPosition]);

  // useEffect(() => {
  //   const onLoadKakaoAPI = () => {
  //     window.kakao.maps.load(() => {
  //       var container = document.getElementById("map");
  //       var options = {
  //         center: new window.kakao.maps.LatLng(
  //           37.55292879706055,
  //           126.96941027090685
  //         ),
  //         level: 3,
  //       };
  //       // map 
  //       var map = new window.kakao.maps.Map(container, options);

  //       if (selectedPosition) {
  //         const moveLatLon = new window.kakao.maps.LatLng(
  //           selectedPosition?.y,
  //           selectedPosition?.x
  //         );
  //         map.panTo(moveLatLon);
  //         console.log(selectedPosition);
  //       }
  //     });
  //   };
  //   //    
  //   kakaoMapScript.addEventListener("load", onLoadKakaoAPI);
  // }, [selectedPosition]);

  return (
    <MapContainer>
      <MapView id="map"></MapView>
    </MapContainer>
  );
}

const MapContainer = styled.div``;

const MapView = styled.div`
  width: 100%;
  height: 400px;
  @media (min-width: 481px) {
    max-width: 600px;
    height: 600px;
  }
`;
