import { Avatar, Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import styled from "styled-components";
import NormalButton from "../../../atomic/NormalButton";
import ToolTips from "../../ToolTips";
import { isBrowser } from "react-device-detect";
import rSocial from "../../../../assets/images/social.png";
import rEcnomy from "../../../../assets/images/economy.png";
import rNovel from "../../../../assets/images/poet.png";
import rNotNovel from "../../../../assets/images/idea.png";
import ErrorIcon from "@mui/icons-material/Error";
import emojiCategory from "../../../../assets/images/categories.png";
import { LoginContext } from "../../../../utils/providers/login/LoginContext";
import RecommandBookCard from "../../modal/RecommandBookCard";
import book1 from "../../../../assets/images/book1.jpg";
import book2 from "../../../../assets/images/book2.jpg";
import book3 from "../../../../assets/images/book3.jpg";
import book4 from "../../../../assets/images/book4.jpg";

import { recommandGpt } from "../../../../utils/gpt";
import { searchBook } from "../../../../utils/book";

export default function RoleContent({ handleClickOpen }) {
  const replaceList = [
    {
      img: rSocial,
      name: " ",
      title: "   ",
      contents:
        "    10,          ,      ",
      thumbnail: book3,
      createAt: "2023 11 24",
    },
    {
      img: rEcnomy,
      name: " ",
      title: "    ",
      contents:
        "      .  , ,  . AI       ,        .",
      thumbnail: book2,
      createAt: "2024 01 24",
    },
    {
      img: rNotNovel,
      name: " ",
      title: "  ",
      contents:
        "2023 8          1 . ",
      thumbnail: book1,
      createAt: "2023 09 07",
    },
    {
      img: rNovel,
      name: " ",
      title: " ",
      contents:
        "           .               .    ,       ,     . ",
      thumbnail: book4,
      createAt: "2023 08 30",
    },
  ];

  const bookCategory = [
    {
      type: 0,
      img: rSocial,
      name: " ",
    },
    {
      type: 1,
      img: rEcnomy,
      name: " ",
    },
    {
      type: 2,
      img: rNotNovel,
      name: " ",
    },
    {
      type: 3,
      img: rNovel,
      name: " ",
    },
  ];

  const tipTitle = null;
  const tipDesc = (
    <span>
      GPT   !
      <br />
          
    </span>
  );

  const [modalOpen, setModalOpen] = useState(false);
  const [modalInfo, setModalInfo] = useState("");

  const [todayBook, setTodayBook] = useState();
  const [title, setTitle] = useState();
  const [desc, setDesc] = useState();
  const [url, setUrl] = useState();
  const [thum, setThum] = useState();
  const [gptRecommandData, setGptRecommandData] = useState([]);
  const [selectedItem, setSelectedItem] = useState("mid");
  const [isLoading, setIsLoading] = useState(true);

  //     
  const handleUsesItemDetail = (index) => {
    setModalOpen((prev) => !prev);

    if (!todayBook[index]) {
      setModalInfo(replaceList[index]);
    } else {
      setModalInfo(todayBook[index]);
    }
  };

  //      
  const convertToObjects = async (text) => {
    let splitText = text?.split(", ");

    let objectBook = splitText.map((item) => {
      const [category, rest] = item.split(": ");
      const [title, author] = rest.split(" / ");

      return {
        category: category?.trim(),
        title: title?.replace(/'/g, "").trim(),
        author: author?.trim(),
      };
    });

    setGptRecommandData(objectBook);
    setIsLoading(false);
    handleRecommanList();
  };

  const handleLength = () => {
    if (isBrowser) {
      if (desc && desc.length >= 80) {
        return desc.substr(0, 80) + "...";
      } else {
        return desc;
      }
    } else {
      if (desc && desc.length >= 55) {
        return desc.substr(0, 55) + "...";
      } else {
        return desc;
      }
    }
  };

  const handleRecommanList = async () => {};

  useEffect(() => {
    (async () => {
      try {
        let promise = gptRecommandData.map((item, index) => {
          return searchBook(item.title + item.author);
        });

        let result = await Promise.all(promise);

        setTodayBook(
          result.map((item) => {
            return item[0];
          })
        );
      } catch (error) {
        console.log("error ", error);
      }
    })();
  }, [isLoading]);

  useEffect(() => {
    (async () => {
      let gptData = await recommandGpt(
        "   , , ,  1         ===  : '  /  ', : '  /  ',"
      );
      // let gptData =
      //   ": '  ', : ' / ', : '   / ', : '  /  ', : '   / '";

      await convertToObjects(gptData);
    })();
  }, []);

  return (
    <RoleSection>
      <Title variant="">
        <img style={{ width: "25px" }} src={emojiCategory} alt=""></img>
        
        <Typography sx={{ margin: "0 0 0 7px" }} variant="" color={"primary"}>
          
        </Typography>
        {isBrowser ? (
          <ToolTips position={"right"} title={tipTitle} desc={tipDesc}>
            <CusNoticeIcon color="" />
          </ToolTips>
        ) : (
          <NormalButton
            callBackFunc={(e) => {
              handleClickOpen(tipTitle, tipDesc);
            }}
            buttonTitle={<CusNoticeIcon color="" />}
          ></NormalButton>
        )}
      </Title>
      <RoleList>
        {bookCategory?.map((role, index) => {
          return (
            <RoleListItem
              onClick={() => {
                handleUsesItemDetail(index);
              }}
              key={index}
            >
              <img className="icon" src={role.img} alt=" "></img>
              <RoelName variant="subtitle1">{role.name}</RoelName>
            </RoleListItem>
          );
        })}

        {modalOpen && (
          <RecommandBookCard
            modalOpen={modalOpen}
            setModalOpen={setModalOpen}
            modalInfo={modalInfo}
          />
        )}
      </RoleList>
    </RoleSection>
  );
}

const RoleSection = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 0;
  margin-bottom: 10px;
`;

const RoleList = styled.ul`
  display: flex;
  width: 100%;
  padding: 10px 0;

  overflow-x: auto;

  @media (min-width: 481px) {
  }
`;

const RoleListItem = styled.li`
  display: flex;
  flex-direction: column;
  flex: 0 0 30%;

  align-items: center;
  justify-content: center;
  margin: 0;

  img.icon {
    width: 75px;
    margin: 10px 0;
  }

  @media (min-width: 481px) {
    flex: 0 0 15%;
  }

  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }

  &.roleUser {
    margin-left: 10%;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      width: 2px;
      height: 100%;
      left: 5px;
      top: 0;
      background: #dcdcdc;
    }

    @media (min-width: 481px) {
      &:after {
        content: "";
        position: absolute;
        width: 2px;
        height: 100%;
        left: -50%;
        top: 0;
        background: #dcdcdc;
      }
    }
  }
`;

const Title = styled(Typography)`
  display: flex;
  align-items: center;
  width: 100%;

  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.75px;

  margin-bottom: 20px;

  & img {
    margin-right: 5px;
  }
`;

const RoleImg = styled(Avatar)`
  /* border: 1px solid #dcdcdc; */
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
  /* overflow: none; */
  & img {
    width: 55px;
    height: 55px;
    object-fit: contain;
  }
`;

const RoelName = styled(Typography)`
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
`;
const CusNoticeIcon = styled(ErrorIcon)`
  color: #3cb371;
`;
